<template>
  <div class="base-table">
    <div class="container">
      <div class="base-table__container d-flex flex-column">
        <div class="row d-flex align-center justify-space-between">
          <div class="d-flex" style="gap: 20px">
            <div class="number col">
              <p class="label number__label">Номер: </p>
              <v-text-field v-model="number"/>
            </div>
            <div class="date col">
              <p class="label date__label">Дата: </p>
              <v-menu
                ref="dateMenu"
                v-model="dateMenu"
                :close-on-content-click="false"
                transition="scale-transition"
                offset-y
                min-width="auto"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date"
                    append-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="date"
                  :active-picker.sync="activePicker"
                  :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                  min="1950-01-01"
                ></v-date-picker>
              </v-menu>
            </div>
          </div>

          <div class="date col">
            <p class="label date__label">Дата проводки: </p>
            <v-menu
              ref="postingDateMenu"
              v-model="postingDateMenu"
              :close-on-content-click="false"
              transition="scale-transition"
              offset-y
              min-width="auto"
            >
              <template v-slot:activator="{ on, attrs }">
                <v-text-field
                  v-model="postingDate"
                  append-icon="mdi-calendar"
                  required
                  readonly
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </template>
              <v-date-picker
                v-model="postingDate"
                :active-picker.sync="activePicker"
                :max="(new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)"
                min="1950-01-01"
              ></v-date-picker>
            </v-menu>
          </div>
        </div>

        <div class="row d-flex align-center justify-space-between">
          <div class="sender-account col">
            <p class="label sender-account__label">Счёт отправителя: </p>
            <v-select
              required
              :items="[]"
              class="sender-account__select"
            ></v-select>
            <v-text-field v-model="number" class="sender-account__name"/>
          </div>
        </div>

        <div class="row d-flex align-center justify-space-between">
          <div class="d-flex" style="width: 100%;">
            <div class="correspondent col">
              <p class="label correspondent__label">Корреспондент: </p>
              <v-text-field v-model="correspondent"/>
            </div>
          </div>
        </div>

        <div class="row d-flex align-center justify-space-between">
          <div class="d-flex" style="width: 100%;">
            <div class="income col">
              <p class="label income__label">Наим. дохода: </p>
              <v-text-field v-model="income"/>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BaseTable',
  data: () => ({
    number: '',
    date: '',
    dateMenu: '',
    activePicker: '',
    postingDateMenu: '',
    postingDate: '',
    correspondent: '',
    income: '',
  })
}
</script>

<style lang="scss" scoped>
.base-table {
  margin: 20px 0;

  &__container {
    padding: 0 20px;
  }
}

.row {
  display: flex;
  gap: 20px;
  padding: 10px 0;
}

.label {
  width: 200px;
}

.col {
  display: flex;
  align-items: center;
  gap: 10px;
}

.date__label {
  width: auto;
}

.sender-account {
  width: 100%;

  &__select {
    flex: 1 1 15%;
  }

  &__name {
    flex: 1 1 60%;
  }
}

.correspondent {
  width: 100%;
}

.income {
  width: 100%;
}
</style>